body {
    background: #000;
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    color: #fff;
}

#root {
    width: 1000px;
    margin: 150px auto 0 auto;
}

.MF-poster {
    position: fixed;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    margin: auto;
    overflow: auto;
    z-index: -1000;
}

.MF-poster-overlay {
    position: fixed;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    margin: auto;
    overflow: auto;
    z-index: -10;
    background-image: -webkit-gradient(linear,left top, left bottom,color-stop(15%, rgba(0,0,0,.85)),color-stop(40%, rgba(0,0,0,.2)),color-stop(90%, #000));
    background-image: -webkit-linear-gradient(rgba(0,0,0,.85) 15%,rgba(0,0,0,.2) 40%,#000 90%);
    background-image: -o-linear-gradient(rgba(0,0,0,.85) 15%,rgba(0,0,0,.2) 40%,#000 90%);
    background-image: linear-gradient(rgba(0,0,0,.85) 15%,rgba(0,0,0,.2) 40%,#000 90%);
    background-attachment: fixed;
}

.MF-list {
    display: -ms-flexbox;
    display: flex;
    list-style-type: none;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    overflow-x: auto;
}

.MF-list-item {
    width: 20%;
    margin: 0 5px;
    cursor: pointer;
}

.MF-list-item:first-child {
    width: 20%;
    margin: 0 5px 0 0;
}

.MF-list-item img {
    height: 200px;
}

.MF-selected-movie {
    display: -ms-flexbox;
    display: flex;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width : 320px) and (max-device-width : 674px) {
    .MF-list-item {
        width: 50%;
        margin: 0 5px;
        cursor: pointer;
    }

    .MF-list-item:first-child {
        width: 50%;
        margin: 0 5px 0 0;
    }
}


